import Vue from 'vue'
import Router from 'vue-router'

import index from '@/pages/index.vue'
import layout from '@/pages/layout.vue'
import button from '@/pages/button.vue'
import cell from '@/pages/cell.vue'
import popup from '@/pages/popup.vue'

Vue.use(Router)

export default new Router({
  routes: [
    // 
    {
      path: '/',
      name: '',
      component: index
    },
    // 
    {
      path: '/layout',
      label: '',
      name: 'Layout ',
      component: layout
    },
    {
      path: '/button',
      label: '',
      name: 'Button ',
      component: button
    },
    {
      path: '/cell',
      label: '',
      name: 'Cell ',
      component: cell
    },
    {
      path: '/popup',
      label: '',
      name: 'Popup ',
      component: popup
    },
    // 
    {
      path: '/checkbox',
      label: '',
      name: 'Checkbox ',
      component: index
    },
    {
      path: '/field',
      label: '',
      name: 'Field ',
      component: index
    },
    {
      path: '/switch',
      label: '',
      name: 'Switch ',
      component: index
    },
    {
      path: '/rate',
      label: '',
      name: 'Rate ',
      component: index
    },
    {
      path: '/checker',
      label: '',
      name: 'Checker ',
      component: index
    },
    {
      path: '/upload',
      label: '',
      name: 'Upload ',
      component: index
    },
    {
      path: '/range',
      label: '',
      name: 'Range ',
      component: index
    },
    {
      path: '/select',
      label: '',
      name: 'Select ',
      component: index
    },
    // 
    {
      path: '/swipe',
      label: '',
      name: 'Swipe ',
      component: index
    },
    {
      path: '/loading',
      label: '',
      name: 'Loading ',
      component: index
    },
    {
      path: '/collapse',
      label: '',
      name: 'Collapse ',
      component: index
    },
    {
      path: '/progress',
      label: '',
      name: 'Progress ',
      component: index
    },
    {
      path: '/price',
      label: '',
      name: 'Price ',
      component: index
    },
    {
      path: '/panel',
      label: '',
      name: 'Panel ',
      component: index
    },
    {
      path: '/recycleList',
      label: '',
      name: 'RecycleList ',
      component: index
    },
    // 
    {
      path: '/toast',
      label: '',
      name: 'Toast ',
      component: index
    },
    {
      path: '/dialog',
      label: '',
      name: 'Dialog ',
      component: index
    },
    {
      path: '/indicator',
      label: '',
      name: 'Indicator ',
      component: index
    },
    // 
    {
      path: '/tabs',
      label: '',
      name: 'Tabs ',
      component: index
    },
    {
      path: '/header',
      label: '',
      name: 'Header ',
      component: index
    },
    {
      path: '/search',
      label: '',
      name: 'Search ',
      component: index
    },
  ]
})


